@tailwind base;
@tailwind components;
@tailwind utilities;

/* for later
font-family: 'Fraunces', serif;

font-family: 'Montserrat', sans-serif;
*/

/* Directives */

@layer utilities {
  .standard-text {
    @apply text-dk-gry-blu font-medium font-sans;
  }
}

@layer components {
  .tagline {
    @apply standard-text uppercase tracking-[5px];
  }

  .bold {
    @apply font-serif text-[3.2rem] leading-[3.2rem];
  }
}

/* Resets */
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

html,
body,
#root {
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a:link,
a:visited {
  color: inherit;
  text-decoration: none;
}

button {
  border: none;
  background: none;
  display: block;
  font-size: inherit;
}

/* defaullt Attribution styling */
.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}
